import React from 'react';

export default () => (
    <div>
        <h1>Hi! Welcome to my website.</h1>
        <p>
            As you can see this website is far from complete and that will always be like this! I love to play around
            with lots of new stuff and I use this website as my playground. For example at this moment I'm building this
            website using <a href="https://www.gatsbyjs.org/">GatsbyJS</a>, a static site generator.
        </p>
    </div>
);



